<template>
    <p-fieldset legend="Fertilization" id="fertilization" class="p-p-1">
        <p-splitter style="height: calc(100vh - 160px)">
            <p-splitter-panel :size="80" class="p-p-3">
                <fertilization-grid />
            </p-splitter-panel>
            <p-splitter-panel :size="20">
                <fertilization-tree-view />
            </p-splitter-panel>
        </p-splitter>

        <born-fertilization v-if="isBornCycle" />

        <fertilization-embryo-detail-dialog />
    </p-fieldset>
</template>

<script>
import { computed, provide, ref } from "vue";
import { useStore } from "vuex";

import FertilizationGrid from "@/components/fertilization/grid.vue";
import BornFertilization from "@/components/born/fertilization.vue";
import FertilizationEmbryoDetailDialog from "@/components/fertilization/embryo-detail-dialog.vue";
import FertilizationTreeView from "@/components/fertilization/tree-view.vue";

export default {
    components: {
        FertilizationGrid,
        BornFertilization,
        FertilizationEmbryoDetailDialog,
        FertilizationTreeView
    },
    setup() {
        const store = useStore();

        const isBornCycle = computed(
            () => store.getters["artCycle/isBornCycle"]
        );

        const embryoDetailDialog = ref(false);
        provide("embryoDetailDialog", embryoDetailDialog);

        const openEmbryoDetailDialog = () => (embryoDetailDialog.value = true);
        provide("openEmbryoDetailDialog", openEmbryoDetailDialog);
        const closeEmbryoDetailDialog = () =>
            (embryoDetailDialog.value = false);
        provide("closeEmbryoDetailDialog", closeEmbryoDetailDialog);

        return {
            isBornCycle
        };
    }
};
</script>

<style>
#fertilization .p-component {
    font-size: 0.85rem;
}
</style>
